import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
// import * as store from '../store'
import Login from '../views/Login.vue'
import Student from '../views/Student.vue'
import Teacher from '../views/Teacher.vue'
import InternshipApplication from '../views/InternshipApplication.vue'
import CompanyApplication from '../views/CompanyApplication.vue'
import PrivacyPolicy from '../views/PrivacyPolicy.vue'
import NotFound from '../views/404.vue'
import test from '../views/test.vue'
import PDF from '../views/Pdf.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login,
    beforeEnter(to, from, next) {
      if (store.getters.isAuthenticated) {
        next();
      } else {
        next();
      }
    }
  },
  {
    path: '/opilane',
    name: 'opilane',
    component: Student,
    beforeEnter(to, from, next) {
      if (store.getters.isAuthenticated) {
        if (store.getters.role === 'student') {
          next()
        } else {
          next(from)
        }
      } else {
        next({name: 'login'})
      }
    }
  },
  {
    path: '/koordinaator',
    name: 'koordinaator',
    component: Teacher,
    beforeEnter(to, from, next) {
        if (store.getters.isAuthenticated) {
          if (store.getters.role === 'admin') {
            next()
          } else {
            next(from)
          }
        } else {
          next({name: 'login'})
        }
    }
  },
  {
    path: '/praktikataotlus',
    name: 'praktikataotlus',
    component: InternshipApplication,
    beforeEnter(to, from, next) {
      if (store.getters.isAuthenticated) {
        if (store.getters.role === 'student') {
          next()
        } else {
          next(from)
        }
      } else {
        next({name: 'login'})
      }
    }
  },
  {
    path: '/taotlus/:id',
    name: 'taotlus',
    component: CompanyApplication,
  },
  {
    path: '/test',
    name: 'test',
    component: test
  },
  {
    path: '/pdf/:id',
    name: 'pdf',
    component: PDF,
    meta: { hideHeader: true, hideFooter: true}
  },
  {
    path: '/privaatsustingimused',
    name: 'privaatsustingimused',
    component: PrivacyPolicy,
  },
  {
    path: '/*',
    name: '404',
    component: NotFound,
  }
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
